import {geArticleComments} from '@/api/article.js'
export default {
  name: 'ArticleComments',
  props:{
    article: {
      required: true,
      type: Object
    }
  },
  data () {
    return {
      comments: ''
    }
  },
  async mounted () {
    console.log(this.article, 'article');
    const {data} = await geArticleComments(this.article.slug)
    this.comments = data.comments
    console.log(data, 'data');
  }
};
